"use client";

import { useMemo, useState } from "react";
import { motion, AnimatePresence, LayoutGroup } from "framer-motion";
import Link from "next/link";

/** Filters like the reference page */
const FILTERS = [
  { key: "all", label: "All" },
  { key: "vinyl", label: "Printed Vinyl & Signs" },
  { key: "wraps", label: "Vehicle Wraps" },
  { key: "glass", label: "Windows & Glass" },
  { key: "walls", label: "Wall Wraps" },
];

/** Cards (swap images/titles/links as needed) */
const ITEMS = [
  {
    id: "vinyl-1",
    title: "Exterior Signage",
    catKey: "vinyl",
    cat: "Printed Vinyl & Signs",
    img: "https://images.unsplash.com/photo-1512295767273-ac109ac3acfa?q=80&w=1400&auto=format&fit=crop",
    href: "/services/printed-vinyl-signs",
  },
  {
    id: "wraps-1",
    title: "Full Vehicle Wrap",
    catKey: "wraps",
    cat: "Vehicle Wraps",
    img: "https://images.unsplash.com/photo-1503376780353-7e6692767b70?q=80&w=1400&auto=format&fit=crop",
    href: "/services/vehicle-fleet-wrap-design",
  },
  {
    id: "wraps-2",
    title: "Color Change Wrap",
    catKey: "wraps",
    cat: "Vehicle Wraps",
    img: "https://images.unsplash.com/photo-1518020382113-a7e8fc38eac9?q=80&w=1400&auto=format&fit=crop",
    href: "/services/vehicle-fleet-wrap-design",
  },
  {
    id: "glass-1",
    title: "Office Frosted Film",
    catKey: "glass",
    cat: "Windows & Glass",
    img: "https://images.unsplash.com/photo-1507152832244-10d45c7eda57?q=80&w=1400&auto=format&fit=crop",
    href: "/services/windows-glass",
  },
  {
    id: "vinyl-2",
    title: "C-CAN / Street Signage",
    catKey: "vinyl",
    cat: "Printed Vinyl & Signs",
    img: "https://images.unsplash.com/photo-1523419409543-733b092ef2be?q=80&w=1400&auto=format&fit=crop",
    href: "/services/printed-vinyl-signs",
  },
  {
    id: "glass-2",
    title: "One-Way Mirror Tint",
    catKey: "glass",
    cat: "Windows & Glass",
    img: "https://images.unsplash.com/photo-1484704849700-f032a568e944?q=80&w=1400&auto=format&fit=crop",
    href: "/services/windows-glass",
  },
  {
    id: "walls-1",
    title: "Interior Wall Wrap",
    catKey: "walls",
    cat: "Wall Wraps",
    img: "https://images.unsplash.com/photo-1519710164239-da123dc03ef4?q=80&w=1400&auto=format&fit=crop",
    href: "/services/printed-vinyl-signs",
  },
  {
    id: "wraps-3",
    title: "Fleet Decals",
    catKey: "wraps",
    cat: "Vehicle Wraps",
    img: "https://images.unsplash.com/photo-1503376780353-7e6692767b70?q=80&w=1400&auto=format&fit=crop",
    href: "/services/vehicle-fleet-wrap-design",
  },
];

export default function ServicesShowcase() {
  const [active, setActive] = useState("all");
  const visible = useMemo(
    () =>
      active === "all" ? ITEMS : ITEMS.filter((it) => it.catKey === active),
    [active]
  );

  return (
    <section
      id="services"
      className="relative w-full overflow-hidden bg-neutral-950 py-14 sm:py-16"
    >
      {/* soft gradient glows */}
      <div className="pointer-events-none absolute inset-0">
        <div className="absolute -top-24 -left-24 h-72 w-72 rounded-full bg-gradient-to-br from-fuchsia-600 via-amber-400 to-cyan-400 opacity-20 blur-3xl" />
        <div className="absolute -bottom-24 -right-24 h-72 w-72 rounded-full bg-gradient-to-br from-cyan-400 via-amber-400 to-fuchsia-600 opacity-10 blur-3xl" />
      </div>

      <div className="relative z-10 mx-auto max-w-6xl px-4">
        {/* Heading */}
        <div className="text-center">
          <h2 className="text-3xl sm:text-4xl font-extrabold tracking-tight text-white">
            Our Services
          </h2>
          <p className="mt-2 text-white/80">
            Design • Print • Install — built for durability and bold impact.
          </p>
        </div>

        {/* Filters */}
        <div className="mt-8 flex flex-wrap items-center justify-center gap-2 sm:gap-3">
          <LayoutGroup id="filters">
            {FILTERS.map((f) => {
              const isActive = active === f.key;
              return (
                <motion.button
                  key={f.key}
                  layout
                  onClick={() => setActive(f.key)}
                  className={`relative rounded-full border px-4 py-2 text-sm transition ${
                    isActive
                      ? "border-transparent text-black"
                      : "border-white/20 text-white/80 hover:text-white"
                  }`}
                  style={{ WebkitTapHighlightColor: "transparent" }}
                >
                  {isActive && (
                    <motion.span
                      layoutId="pill"
                      className="absolute inset-0 rounded-full bg-gradient-to-r from-fuchsia-500 via-amber-400 to-cyan-400"
                      transition={{
                        type: "spring",
                        stiffness: 350,
                        damping: 30,
                      }}
                    />
                  )}
                  <span className="relative z-10">{f.label}</span>
                </motion.button>
              );
            })}
          </LayoutGroup>
        </div>

        {/* Grid */}
        <LayoutGroup id="grid">
          <motion.div
            layout
            className="mt-10 grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-4"
          >
            <AnimatePresence initial={false}>
              {visible.map((item, idx) => (
                <motion.div
                  key={item.id}
                  layout
                  initial={{ opacity: 0, y: 18, scale: 0.98 }}
                  animate={{ opacity: 1, y: 0, scale: 1 }}
                  exit={{ opacity: 0, y: 18, scale: 0.98 }}
                  transition={{ duration: 0.35, delay: idx * 0.03 }}
                >
                  <Card item={item} />
                </motion.div>
              ))}
            </AnimatePresence>
          </motion.div>
        </LayoutGroup>
      </div>

      {/* Watermark */}
      <div className="pointer-events-none select-none text-center mt-10 font-extrabold uppercase tracking-[0.2em] text-white/10 text-5xl sm:text-7xl">
        SERVICES
      </div>
    </section>
  );
}

function Card({ item }) {
  return (
    <Link
      href={item.href}
      className="group block relative overflow-hidden rounded-2xl"
    >
      <div className="relative aspect-[4/3] w-full overflow-hidden rounded-2xl border border-white/10 bg-white/5">
        <img
          src={item.img}
          alt={item.title}
          className="h-full w-full object-cover transition-transform duration-700 group-hover:scale-105"
          loading="lazy"
          decoding="async"
        />
        <div className="pointer-events-none absolute inset-0 bg-gradient-to-t from-black/70 via-black/10 to-transparent opacity-0 transition-opacity duration-300 group-hover:opacity-100" />
        <div className="absolute inset-x-0 bottom-0 p-4">
          <div className="translate-y-2 opacity-0 transition-all duration-300 group-hover:translate-y-0 group-hover:opacity-100">
            <div className="text-xs text-white/70">{item.cat}</div>
            <div className="mt-0.5 text-lg font-semibold text-white">
              {item.title}
            </div>
            <div className="mt-2 inline-flex items-center text-sm font-medium text-white/90">
              <span className="underline underline-offset-4 decoration-white/40">
                Learn more
              </span>
              <svg
                className="ml-1 h-4 w-4 transition-transform group-hover:translate-x-0.5"
                viewBox="0 0 24 24"
                fill="none"
                aria-hidden
              >
                <path
                  d="M5 12h14M13 5l7 7-7 7"
                  stroke="currentColor"
                  strokeWidth="1.6"
                  strokeLinecap="round"
                />
              </svg>
            </div>
          </div>
        </div>
      </div>
    </Link>
  );
}
